<template>
    <v-container>
      <v-row>
        <!-- Columna Izquierda -->
        <v-col sm="6" md="6" lg="6">
          <v-form>
            <!-- Campo para Nombre -->
            <v-text-field
              v-model="nombre"
              label="Nombre"
              outlined
              dense
            ></v-text-field>
  
            <!-- Campo para RUT -->
            <v-text-field
              v-model="rut"
              label="RUT"
              outlined
              dense
              @input="formatRUT"
              :rules="[validateRUT]"
              maxlength="12"
            ></v-text-field>
  
            <!-- Checkbox Group para las Instituciones -->
            <div class="checkbox-container">
              <v-checkbox v-model="instituciones" :value="'Registro Civil'" label="Registro Civil"></v-checkbox>
              <v-checkbox v-model="instituciones" :value="'SII'" label="SII"></v-checkbox>
              <v-checkbox v-model="instituciones" :value="'Tesorera'" label="Tesorera"></v-checkbox>
              <v-checkbox v-model="instituciones" :value="'Servel'" label="Servel"></v-checkbox>
              <v-checkbox v-model="instituciones" :value="'PDI'" label="PDI"></v-checkbox>
              <v-checkbox v-model="instituciones" :value="'Carabineros'" label="Carabineros"></v-checkbox>
              <v-checkbox v-model="instituciones" :value="'Otros'" label="Otros"></v-checkbox>
            </div>
          </v-form>
          <v-btn :to="'/'">Volver</v-btn>
        </v-col>
  
        <!-- Columna Derecha -->
        <v-col sm="6" md="6" lg="6">
          <h2>Oficio</h2>
          <div ref="oficioText">
            <!-- Texto adicional antes de los checkboxes -->
            <p class="intro-text"><strong>Resolviendo presentacin de fecha 21 de septiembre de 2024, a folio 10:</strong></p>
            <div v-for="(institucion, index) in instituciones" :key="index">
              <p>{{ generarTexto(institucion, false) }}</p>
            </div>
          </div>
          <div class="button">
            <v-tooltip v-model="tooltipVisible" bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  v-on="on"
                  @click="copyToClipboard"
                >
                  Copiar
                </v-btn>
              </template>
              <span>Texto copiado al portapapeles</span>
            </v-tooltip>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  export default {
    name: 'HelloWorld',
  
    data() {
      return {
        nombre: '',
        rut: '',
        instituciones: [],
        tooltipVisible: false, // Controla la visibilidad del tooltip
      };
    },
  
    methods: {
      formatRUT() {
        let rutValue = this.rut.replace(/[^\dkK]/g, ''); // Remover caracteres invlidos
  
        if (rutValue.length > 9) {
          rutValue = rutValue.slice(0, 9);
        }
  
        let part1 = '';
        let part2 = '';
        let part3 = '';
        let checkDigit = '';
  
        if (rutValue.length > 1) {
          checkDigit = rutValue.slice(-1); // ltimo dgito
          part3 = rutValue.slice(-4, -1); // Los 3 dgitos antes del dgito verificador
          part2 = rutValue.slice(-7, -4); // Los 3 dgitos anteriores
          part1 = rutValue.slice(0, -7); // Los 1 o 2 dgitos restantes
        } else {
          part1 = rutValue;
        }
  
        if (part1.length > 2) {
          part2 = part1.slice(-3) + part2;
          part1 = part1.slice(0, -3);
        }
  
        this.rut = `${part1}.${part2}.${part3}-${checkDigit}`.toUpperCase();
      },
  
      validateRUT(rut) {
        const rutRegex = /^[0-9]{1,2}\.[0-9]{3}\.[0-9]{3}-[0-9kK]{1}$/;
        return rutRegex.test(rut) || 'El RUT ingresado no es vlido';
      },
  
      generarTexto(institucion, conFormato = false) {
        const nombre = conFormato ? `<strong>${this.nombre}</strong>` : this.nombre;
        const rut = conFormato
          ? `<strong>cdula de identidad nmero ${this.rut}</strong>`
          : `cdula de identidad nmero ${this.rut}`;
  
        if (institucion === 'Registro Civil') {
          return `Atendido el convenio vigente entre el Poder Judicial y el Servicio de Registro Civil e Identificacin y, habindose consultado en lnea el domicilio de la parte demandada, ${nombre}, ${rut}, se informa que ste corresponde a XXXXXXX.`;
        } else if (institucion === 'SII') {
          return `Atendido el convenio vigente entre el Poder Judicial y el Servicio de Impuestos Internos, aplquese la nomenclatura [1390], a fin de generar informe de domicilio de ${nombre}, ${rut}.`;
        } else if (institucion === 'Tesorera') {
          return `En cuanto a Tesorera General de la Repblica y atendido lo dispuesto expresamente por Circular N5 DL N3 de fecha 16 de Marzo de 2016 mediante la cual se permite al Poder Judicial ingresar a la base de datos de Tesorera General de la Repblica para los efectos de realizar la consulta de domicilio de las personas que correspondan en determinado juicio, como se pide, y habindose consultado el domicilio de ${nombre}, ${rut}, este corresponde a XXXXXXXX.`;
        } else if (institucion === 'Servel') {
          return `En cuanto, al Servicio Electoral, como se pide, ofciese a fin de informar el domicilio actual de ${nombre}, ${rut}. Pngase a disposicin de la parte solicitante oficio material, a travs de la Oficina Judicial Virtual, al tercer da hbil desde la notificacin por el estado diario de la presente resolucin.`;
        } else if (institucion === 'PDI') {
          return `Como se pide, ofciese a Polica de Investigaciones de Chile, Departamento de Extranjera, a fin de que informe las entradas y salidas del pas de la parte demandada ${nombre}, ${rut}, desde el ao 2023 a la actualidad.`;
        } else if (institucion === 'Carabineros') {
          return `Respecto al oficio solicitado para la Direccin General de Carabineros y Comisara Virtual, y perteneciendo stas a Carabineros de Chile, y por usar la misma de base de datos que el Registro Civil, no ha lugar por innecesario.`;
        } else if (institucion === 'Otros') {
          return `Con respecto a los oficios solicitados a XXXXX, careciendo de interconexin, y no correspondiendo a una institucin que entregue informacin sobre domicilios, no ha lugar.`;
        }
      },
  
      copyToClipboard() {
        const range = document.createRange();
        range.selectNode(this.$refs.oficioText);
        window.getSelection().removeAllRanges(); // Limpia seleccin previa
        window.getSelection().addRange(range); // Selecciona el contenido
  
        try {
          document.execCommand('copy'); // Ejecuta la copia
          this.tooltipVisible = true;
          setTimeout(() => {
            this.tooltipVisible = false;
          }, 2000);
        } catch (err) {
          console.error('No se pudo copiar el texto', err);
        }
  
        window.getSelection().removeAllRanges(); // Limpia la seleccin
      },
    },
  };
  </script>
  
  <style>
  .checkbox-container {
    display: flex;
    flex-wrap: wrap;
  }
  
  .checkbox-container>* {
    margin-right: 10px;
  }
  
  .button {
    margin-top: 3%;
  }
  
  .resolucion-texto {
    font-family: 'Batang', serif;
    font-size: 12px;
    text-align: justify;
  }
  
  /* Estilo para el texto adicional */
  .intro-text {
    font-family: 'Batang', serif;
    font-size: 12px;
    text-indent: 2em;
  }
  </style>
  