<template>
    <v-container>
      <v-row>
        <!-- Columna Izquierda -->
        <v-col sm="6" md="6" lg="6">
          <v-form>
            <!-- Campo "A lo principal" -->
            <v-select
              v-model="selectedOptions['A lo principal']"
              :items="principalOptions"
              label="A lo principal"
              outlined
              dense
              class="small-select"
            ></v-select>
  
            <!-- Campos "Al primer otros" hasta "Al dcimo otros" -->
            <v-row>
              <v-col cols="6" v-for="(label, index) in otrosiLabels" :key="index">
                <v-select
                  v-model="selectedOptions[label]"
                  :items="otrosiOptions"
                  :label="label"
                  outlined
                  dense
                  class="small-select"
                ></v-select>
              </v-col>
            </v-row>
          </v-form>
          <v-btn :to="'/'">Volver</v-btn>
        </v-col>
  
        <!-- Columna Derecha -->
        <v-col sm="6" md="6" lg="6">
          <h2>Resolucin</h2>
          <div
            ref="resolucionText"
            class="resolucion-texto"
          >
            <p class="indent"><strong>{{ titleText }}</strong></p>
            <div v-html="formattedText"></div>
          </div>
          <div class="button">
            <v-tooltip v-model="tooltipVisible" bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  v-on="on"
                  @click="copyToClipboard"
                >
                  Copiar
                </v-btn>
              </template>
              <span>Texto copiado al portapapeles</span>
            </v-tooltip>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  export default {
  name: 'NuevaResolucion',
  
  data() {
    return {
      principalOptions: [
        'Despchese',
        'Notifquese',
        'Notifquese y requirase personalmente al ejecutado',
      ],
      otrosiOptions: [
        'Seala bienes',
        'Acompaa documentos',
        'Exhrtese',
        'Estse 44',
        'Tngase presente',
        'Tngase por acompaado',
        'Tngase presente y por acompaado',
      ],
      otrosiLabels: [
        'Al primer otros',
        'Al segundo otros',
        'Al tercer otros',
        'Al cuarto otros',
        'Al quinto otros',
        'Al sexto otros',
        'Al sptimo otros',
        'Al octavo otros',
        'Al noveno otros',
        'Al dcimo otros',
      ],
      selectedOptions: {
        'A lo principal': null,
        'Al primer otros': null,
        'Al segundo otros': null,
        'Al tercer otros': null,
        'Al cuarto otros': null,
        'Al quinto otros': null,
        'Al sexto otros': null,
        'Al sptimo otros': null,
        'Al octavo otros': null,
        'Al noveno otros': null,
        'Al dcimo otros': null,
      },
      tooltipVisible: false,
      titleText: 'Resolviendo presentacin de fecha XXXX de XXXX de XXXX a folio 1:',
    };
  },
  
  computed: {
    formattedText() {
      const texts = [];
      const otrosiLabels = this.otrosiLabels;
      const selectedOptions = this.selectedOptions;
  
      // Procesar 'A lo principal' primero
      if (selectedOptions['A lo principal']) {
        const text = this.generarTexto('A lo principal', selectedOptions['A lo principal']);
        if (text) {
          texts.push(text);
        }
      }
  
      // Agrupar otroses consecutivos con la misma opcin
      let currentOption = null;
      let currentLabels = [];
  
      for (let i = 0; i < otrosiLabels.length; i++) {
        const label = otrosiLabels[i];
        const option = selectedOptions[label];
  
        if (option) {
          if (currentOption === null) {
            // Iniciar nuevo grupo
            currentOption = option;
            currentLabels = [label];
          } else if (option === currentOption) {
            // Aadir label al grupo actual
            currentLabels.push(label);
          } else {
            // Generar texto para el grupo actual
            const combinedLabel = this.combineLabels(currentLabels);
            const text = this.generarTexto(combinedLabel, currentOption);
            if (text) {
              texts.push(text);
            }
            // Iniciar nuevo grupo
            currentOption = option;
            currentLabels = [label];
          }
        } else {
          if (currentOption !== null) {
            // Generar texto para el grupo actual
            const combinedLabel = this.combineLabels(currentLabels);
            const text = this.generarTexto(combinedLabel, currentOption);
            if (text) {
              texts.push(text);
            }
            // Resetear grupo actual
            currentOption = null;
            currentLabels = [];
          }
        }
      }
  
      // Procesar ltimo grupo si existe
      if (currentOption !== null && currentLabels.length > 0) {
        const combinedLabel = this.combineLabels(currentLabels);
        const text = this.generarTexto(combinedLabel, currentOption);
        if (text) {
          texts.push(text);
        }
      }
  
      // Unir los textos con '; '
      const concatenatedText = texts.join('; ');
  
      // Construir el HTML final incluyendo "CUANTA:" en negrita
      let finalHtml = '';
      finalHtml += `<p class="indent">${concatenatedText ? concatenatedText + '.' : ''}</p>`;
      finalHtml += `<p class="indent"><strong>CUANTA:</strong></p>`;
  
      return finalHtml;
    },
  },
  
  methods: {
    combineLabels(labels) {
      // Extraer los ordinales
      const ordinals = labels.map(label =>
        label.replace('Al ', '').replace(' otros', '')
      );
  
      // Combinar los ordinales
      let combinedOrdinals = '';
      if (ordinals.length === 1) {
        combinedOrdinals = ordinals[0];
      } else if (ordinals.length === 2) {
        combinedOrdinals = `${ordinals[0]} y ${ordinals[1]}`;
      } else {
        combinedOrdinals =
          ordinals.slice(0, -1).join(', ') + ' y ' + ordinals[ordinals.length - 1];
      }
  
      // Reconstruir el label combinado
      return `Al ${combinedOrdinals} otros`;
    },
  
    generarTexto(label, option) {
      // Etiqueta en negrita
      const boldLabel = `<strong>${label}</strong>`;
      if (label === 'A lo principal') {
        if (option === 'Despchese') {
          return `${boldLabel}: despchese`;
        } else if (option === 'Notifquese') {
          return `${boldLabel}: Notifquese`;
        } else if (option === 'Notifquese y requirase personalmente al ejecutado') {
          return `${boldLabel}: Notifquese y requirase personalmente al ejecutado`;
        }
      } else {
        if (option === 'Seala bienes') {
          return `${boldLabel}: tngase presente, y slo sobre bienes suficientes de la parte ejecutada, la que queda como depositaria provisional`;
        } else if (option === 'Acompaa documentos') {
          return `${boldLabel}: tngase por acompaado el pagar y contrato bajo apercibimiento del artculo 346 N3 del Cdigo de Procedimiento Civil, en custodia bajo el nmero XXXX-2024. A los dems documentos, por acompaados, con citacin`;
        } else if (option === 'Exhrtese') {
          return `${boldLabel}: como se pide, exhrtese va interconexin al Juzgado de Letras de turno en lo Civil de XXXX, a fin de practicarse la notificacin de la demanda al ejecutado, requerirlo de pago y trabar el embargo sobre las especies de su propiedad, de ser necesario y facultndose, en caso de oposicin, a conceder el auxilio de fuerza pblica con facultades de allanamiento y descerrajamiento, adems de todas aquellas diligencias y actuaciones necesarias y conducentes para obtener el cabal y pronto diligenciamiento del presente exhorto`;
        } else if (option === 'Estse 44') {
          return `${boldLabel}: estese a lo dispuesto en el artculo 44 del Cdigo de Procedimiento Civil`;
        } else if (option === 'Tngase presente') {
          return `${boldLabel}: tngase presente`;
        } else if (option === 'Tngase por acompaado') {
          return `${boldLabel}: tngase por acompaado, con citacin`;
        } else if (option === 'Tngase presente y por acompaado') {
          return `${boldLabel}: tngase presente y por acompaado, con citacin`;
        }
      }
      return '';
    },
  
    copyToClipboard() {
      const range = document.createRange();
      range.selectNode(this.$refs.resolucionText);
      window.getSelection().removeAllRanges(); // Limpiar la seleccin previa
      window.getSelection().addRange(range); // Selecciona el contenido
  
      try {
        document.execCommand('copy'); // Ejecuta la copia
        this.tooltipVisible = true;
        setTimeout(() => {
          this.tooltipVisible = false;
        }, 2000);
      } catch (err) {
        console.error('No se pudo copiar el texto', err);
      }
  
      window.getSelection().removeAllRanges(); // Limpia la seleccin
    },
  },
  };
  </script>
  
  <style>
  .button {
  margin-top: 3%;
  }
  
  .resolucion-texto {
  font-family: 'Batang', serif;
  font-size: 12px;
  text-align: justify;
  }
  
  .v-select .v-input__control {
  min-height: 40px;
  }
  
  /* Organizar los selects en dos columnas */
  .v-form {
  display: flex;
  flex-direction: column;
  }
  
  .v-row {
  flex-wrap: wrap;
  }
  
  .v-col {
  padding: 0 5px;
  }
  
  /* Clase para la sangra */
  .indent {
  text-indent: 2em;
  margin-bottom: 1em;
  }
  </style>
  